import mpvueCityPicker from '@/components/uni-ui/mpvue-citypicker/mpvueCityPicker.vue'
import {
	mapActions
} from 'vuex'
export default {
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			isedit: false,
			themeColor: '#FD6801',
			pickerValue: [0, 0, 0],
			index: -1,
			form: {
				name: '',
				phone: '',
				path: '',
				detailPath: '',
				isdefault: false
			}

		}
	},
	onLoad(e) {
		console.log()
		if (e.data) {
			let result = JSON.parse(e.data)
			this.form = result.item
			this.index = result.index
			this.isedit = true
			uni.setNavigationBarTitle({
				title: '修改收货地址'
			})
		}
	},
	// 监听返回
	onBackPress() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
			return true
		}
	},
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	},
	methods: {

		...mapActions(['updatePathAction', 'createPathAction']),
		// 显示三级联动
		showPicker() {
			this.$refs.mpvueCityPicker.show()
		},
		// 三级联动确定
		onConfirm(e) {
			console.log(e)
			this.form.path = e.label
			this.pickerValue = e.value
		},
		// 提交保存
		submit() {
			// 验证表单
			// 创建
			if (this.isedit) {
				this.updatePathAction({
					index: this.index,
					item: this.form
				})
				uni.showToast({
					title: '修改成功'
				});
				uni.navigateBack({
					delta: 1
				})
				return
			}
			this.createPathAction(this.form)
			uni.showToast({
				title: '创建成功'
			});
			uni.navigateBack({
				delta: 1
			})
		}
	}
}
